import { getOrders } from '../features/user/userSlice'
import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'

function Order() {
  const dispatch = useDispatch()

  useEffect(() => {
 dispatch(getOrders())
  },[])
  const state = useSelector(state => state.users.order)
  console.log(state);

  let itemsMap
const userNameMap = state.map(ev => (
  <div key={ev._id} >
     <p>{ev.createdAt}</p>
     <p>{ev.user.name}</p>
     

{itemsMap = ev.items.map(ev => (
  <div key={Math.random()} id='d-f' className='cart' >
    <img src={ev.product.image} alt='product'/>
     <p>{ev.product.name}</p>
     <p>{ev.product.price}</p>
     <p>{ev.product.category}</p>

     <p>{ev.option.size}</p>
     <div style={{backgroundColor: ev.option.color}}/>
  </div>
))}

</div>
))

  return (
    <div>
      {userNameMap}
      {/* {itemsMap} */}
    </div>
  )
}
export default Order