import { useEffect, useRef, useState } from 'react'
import {useDispatch, useSelector} from 'react-redux'
import {useLocation} from 'react-router-dom'
import { getProducts } from '../features/product/productSlice'
import { cart } from "../features/user/userSlice"

function Product(){
  const [err, setErr] = useState("")
  const [color, setColor] = useState(false)
  const [size, setSize] = useState(false)
  const dispatch = useDispatch()
  const location = useLocation()
  const productId = location.pathname.split("/")[2]
  const user = JSON.parse(localStorage.getItem('user'))

  useEffect(() => {
 dispatch(getProducts())
  },[])

  const cartHandler = () => {
    console.log(color, size);
    if(!size || !color) return setErr("Select size and color")
    setErr("")
    dispatch(cart({product: state[0], userId: user._id, userName: user.name , color, size}))
  }

  const state = useSelector(state => state.products.product)

  let map;
  if(state.length > 0 ){
     map = state.map(ev => (
      <div key={ev._id}>
        <p>{ev.name}</p>
        <p>{ev.price}</p>
        <p>{ev.category}</p>
        <img src={ev.image} alt='nike'/>
        <select defaultValue={'DEFAULT'} onChange={(ev) => setColor(ev.target.value)}>
          <option value="DEFAULT" disabled >Select color</option>
          <option value="red">red</option>
          <option value="green">green</option>
          <option value="blue">blue</option>
        </select>
        <select defaultValue={'D'} onChange={(ev) => setSize(ev.target.value)}>
          <option value={"D"} disabled>Select size</option>
          <option value="large">large</option>
          <option value="medium">medium</option>
          <option value="small">small</option>
        </select>
        <button onClick={cartHandler}>Add To Cart</button>
      </div>
    ))
  }

  return(
    <div>
      {map}
      {err}
    </div>
  )
}
export default Product