import { useNavigate } from 'react-router-dom'
import { useDispatch } from 'react-redux';
import { createOrder } from '../features/user/userSlice'
import { DOMAIN } from '../store/url';
import { useEffect } from 'react';

function Cart() {
  const navigate = useNavigate()
  const dispatch = useDispatch()

  const user = JSON.parse(localStorage.getItem(`${DOMAIN}`))
  //  useEffect(() => {
  //  },[])
  // if (!user) navigate('/login')
  if (!user) return navigate('/login')

  if (!user.cart) return <h1>Your cart is empty</h1>
  let sum = 0;
  let map
  if (user && user.cart) {
    map = user.cart.map((ev, i) => (
      <div key={Math.random()} className="cart">
        {i += 1}
        <img src={ev.product.image} />
        <p>{ev.product.name}</p>
        <p>{ev.product.category}</p>
        <p>${ev.product.price}</p>

        <div style={{ backgroundColor: ev.option.color }} />
        <p>{ev.option.size}</p>
        <p>{ev.option.quantity}</p>
        <button>delete</button>
        {sum += ev.product.price}
      </div>
    ))
  }
  // }

  const orderHandler = () => {
    dispatch(createOrder(user))
  }

  return (
    <div>
      {map}
      <button onClick={orderHandler}><h1>Order</h1></button>
    </div>
  )
}
export default Cart