import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import axios from 'axios'
import { URL } from '../../store/url'
const service = require('./userService')


export const getUsers = createAsyncThunk("getUsers", async (thunk) => {
  try {
    const response = await service.getUsers()
  } catch (error) {
    thunk.rejectWithValue(error)
  }
})

export const createUser = createAsyncThunk("sign-up", async(data, thunk) => {
  try{
    const res = await axios.post(`${URL}/user/up`, data)
    return res.data
  }catch(er){
    return thunk.rejectWithValue(er)
  }
})

export const SignIn = createAsyncThunk("sign-in", async(data, thunk) => {
  try{
    const res = await axios.post(`${URL}/user/in`, data)
    return res.data
  }catch(er){
    return thunk.rejectWithValue(er)
  }
})

export const cart = createAsyncThunk("cart", async(data, thunk) => {
  try{
    const res = await axios.put(`${URL}/user/cart`, data)
    return res.data
  }catch(er){
    return thunk.rejectWithValue(er)
  }
})

export const createOrder = createAsyncThunk("order", async(data, thunk) => {
  try{
    console.log(data );
    const res = await axios.post(`${URL}/user/create-order`, data)
    return res.data
  }catch(er){
    return thunk.rejectWithValue(er)
  }
})

export const getOrders = createAsyncThunk("order", async( thunk) => {
  try{
    const res = await axios.get(`${URL}/user/get-orders`)
    return res.data
  }catch(er){
    return thunk.rejectWithValue(er)
  }
})

const initialState = {
  user: [],
  order: [],
  isError: false,
  isSuccess: false,
  isLoading: false
}

export const userSlice = createSlice({
  name: "something",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(getUsers.pending, (state) => {
        state.isLoading = true
      })
      .addCase(getUsers.fulfilled, (state) => {
        state.isSuccess = true;
        state.isLoading = false;
        state.isError = false
      })
      .addCase(getUsers.rejected, (state) => {
        state.isSuccess = false;
        state.isLoading = false;
        state.isError = true
      })

      .addCase(cart.pending, (state) => {
        state.isLoading = true
      })
      .addCase(cart.fulfilled, (state, action) => {
        state.isSuccess = true;
        state.isLoading = false;
        state.isError = false
        localStorage.setItem('user' , JSON.stringify(action.payload) )
        alert("Added to your cart successfully")
      })
      .addCase(cart.rejected, (state) => {
        state.isSuccess = false;
        state.isLoading = false;
        state.isError = true
      })

      .addCase(createUser.pending, (state) => {
        state.isLoading = true
      })
      .addCase(createUser.fulfilled, (state, action) => {
        state.isSuccess = true;
        state.isLoading = false;
        state.isError = false
        alert('User Created Successfully')
      })
      .addCase(createUser.rejected, (state) => {
        state.isSuccess = false;
        state.isLoading = false;
        state.isError = true;
      })

      .addCase(SignIn.pending, (state) => {
        state.isLoading = true
      })
      .addCase(SignIn.fulfilled, (state, action) => {
        state.isSuccess = true;
        state.isLoading = false;
        state.isError = false;
        localStorage.setItem('user' , JSON.stringify(action.payload) )
        alert('logged in successfully')
        window.location.reload()
      })
      .addCase(SignIn.rejected, (state) => {
        state.isSuccess = false;
        state.isLoading = false;
        state.isError = true;
        alert('something went wrong')
      })

      // .addCase(createOrder.pending, (state) => {
      //   state.isLoading = true
      // })
      // .addCase(createOrder.fulfilled, (state, action) => {
      //   state.isSuccess = true;
      //   state.isLoading = false;
      //   state.isError = false;
      //   localStorage.setItem('user' , JSON.stringify(action.payload) )
      //   alert('createOrder')
      // })
      // .addCase(createOrder.rejected, (state) => {
      //   state.isSuccess = false;
      //   state.isLoading = false;
      //   state.isError = true;
      //   alert('something went wrong')
      // })

       .addCase(getOrders.pending, (state) => {
        state.isLoading = true
      })
      .addCase(getOrders.fulfilled, (state, action) => {
        state.isSuccess = true;
        state.isLoading = false;
        state.isError = false;
        state.order = action.payload
      })
      .addCase(getOrders.rejected, (state) => {
        state.isSuccess = false;
        state.isLoading = false;
        state.isError = true;
      })


  }
})

export default userSlice.reducer